}

.dropdown-slider-content a {
  text-decoration: none;
}
.slider-wrapper * {
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}
.idle {
  opacity: 0;
}
.slider-wrapper.nocursor * {
  cursor: none !important;
}


